import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import db from './firebase';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(5),
            width: theme.spacing(40),
            height: theme.spacing("auto"),
            padding: theme.spacing(1)
        },
    },
}));

export default function SavedNote(props) {
    const classes = useStyles();

    // const deleteNote = () => {
    //     props.deleteItem(props.id);
        
    //     console.log(db.collection('todos').doc(props.title));
    // };

    return (
        <div className={classes.root}>
            <Paper elevation={10}>
                <form action="">
                    <h4>{props.text.title}</h4>
                    <p>{props.text.content}</p>
                    <div className="alignButton">
                        <IconButton onClick={event => db.collection('todos').doc(props.text.id).delete()} style={{ backgroundColor: "royalblue", justifyContent: "flex-end", alignItems: "flex-end" }}>
                            <DeleteIcon fontSize="Larger" className="plus-sign" />
                        </IconButton>
                    </div>
                </form>
            </Paper>
        </div>
    );
};