import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';

export default function NewNote(props) {

    const [height, changeHeight] = useState(14);
    const [row, changeRow] = useState(2);
    const [note, setNote] = useState({
        title: "",
        content: ""
    });

    const inputEvent = (event) => {

        const value = event.target.value;
        const name = event.target.name;

        setNote((preData) => {
            return {
                ...preData,
                [name]: value
            };
        });
    };

    const addEvent = () => {
        if (note.title !== "" && note.content !== "") {
            props.passNote(note);
            setNote({
                title: "",
                content: ""
            });
        }
    };

    const incHeight = () => {
        changeHeight(20);
        changeRow(5);
    };

    const decHeight = () => {
        changeHeight(14);
        changeRow(2);
    };

    const useStyles = makeStyles((theme) => ({
        root: {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
            flexWrap: 'wrap',
            '& > *': {
                margin: theme.spacing(5),
                width: theme.spacing(40),
                height: theme.spacing(height),
                padding: theme.spacing(1),
            },
        },
        animate: {
            transition: '.2s'
        }
    }));

    const classes = useStyles();

    return (
        <div className={classes.root} >
            <Paper elevation={10} onFocus={incHeight} onBlur={decHeight} className={classes.animate}>
                <form action="">
                    <input type="text" placeholder="Title" className="titleinput" value={note.title} onChange={inputEvent} name="title" />
                    <textarea cols="42" rows={row} placeholder="Write a note.." value={note.content} onChange={inputEvent} name="content" />
                    <span className="alignButton">
                        <IconButton style={{ backgroundColor: "royalblue" }} onClick={addEvent}>
                            <AddIcon fontSize="" className="plus-sign" />
                        </IconButton>
                    </span>
                </form>
            </Paper>
        </div>
    );
};