import React, { useEffect, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Header from './Header';
import NewNote from './NewNote';
import Footer from './Footer';
import SavedNote from './SavedNote';
import '../src/index.css';
import db from './firebase';

const App = () => {

    const [addItem, setAddItem] = useState([]);

    useEffect(() => {
        //fetch from firebase db
        db.collection('todos').onSnapshot(snapshot => {
            setAddItem(snapshot.docs.map(doc => ({ id: doc.id, title: doc.data().title, content: doc.data().content })));
        });
    }, []);

    const addNote = (note) => {
        // setAddItem((preValue) => {
        //     return [...preValue, note];
        // });

        db.collection('todos').add({
            title: note.title,
            content: note.content,
        });
    };

    const onDelete = (id) => {
        // setAddItem((preValue) =>
        //     preValue.filter((currdata, index) => {
        //         return index !== id;
        //     })
        // );

        db.collection('todos').doc().delete();
    };

    return (
        <>
            <Header />
            <NewNote passNote={addNote} />

            <div className="saved_notes">
                <Grid container spacing={0}>
                    {
                        addItem.map(item => {
                            return (
                                <>
                                    <Grid item xs={12} sm={4}>
                                        <SavedNote text={item} />
                                    </Grid>
                                </>
                            );
                        })
                    }
                </Grid>
            </div>
            <Footer />
        </>
    );
};

export default App;